.person {
}
.person__wrapper {
  display: grid;
  grid-template-columns: max-content max-content;
}

.person__title {
  @include title;
}

.person__years {
  margin: 0;
  margin-left: 10px;
  padding-top: 20px;
  font-size: 20px;
  line-height: 22px;
}

.person__description {
  grid-column: 1/-1;
  width: 100%;
  padding-top: 16px;
  max-width: 1100px;
  display: grid;
  grid-template-columns: 1fr 1fr;

  @media (min-width: 768px) {
    padding-top: 70px;
    grid-template-columns: max-content 410px;
    column-gap: 10%;
  }
}

.person__photo {
  align-self: flex-start;
  max-width: 414px;
  border: 2px solid #bd9869;
}

.person__text {
  height: 500px;
  padding-right: 30px;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    width: 10px;
    border: 1px solid #bd9869;
  }

  &::-webkit-scrollbar-track {
    background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: #bd9869;
  }

  @media (min-width: 768px) {
    h2 {
      margin: 0;
      font-family: $title-font;
      font-weight: 400;
      font-size: 36px;
      line-height: 42px;
      text-transform: uppercase;
      color: #ffffff;
    }

    p {
      margin: 0;
      margin-bottom: 20px;
      font-size: 12px;
      line-height: 19px;
      color: #ffffff;
    }
  }
}
